<script>
   /*************************************************************/
   /* AppPlots component for new app                           */
   /* -----------------------------------                       */
   /* defines plot components, controls and parameters          */
   /*************************************************************/

   import { getDefaults } from 'svelte-controls-basic';
   import { Container, Button } from 'svelte-controls-basic';
   import { Vector } from 'mdatools/arrays';
   import { dnorm } from 'mdatools/distributions';

   // global plot router
   import PlotsRouter from '../../../shared/plots/PlotsRouter.svelte';

   // import plot components
   import PlotDiscrimination from '../plots/PlotDiscrimination.svelte';
   import PlotScores from '../plots/PlotScores.svelte';
   import PlotDistance from '../plots/PlotDistance.svelte';

   // import plot controls components
   import { getPlotDiscriminationControls, getPlotScoresControls, getPlotDistanceControls } from '../plots/Controls.svelte.js';

   let {
      res,
      model,
      data,
      scalars,
      markOutlier,
      showContributions
   } = $props();


   const plsres = $derived({'new': res.new.pls});
   const dares = $derived({'new': res.new.da});
   const ncomp = $derived(model.da.ncomp);


   // JSON with plot components (scope is needed for providing correct properties)
   const plots = $derived({
      // objects
      pred: {label: 'Discrimination', scope: 'objects', component: PlotDiscrimination, buttons: buttons1},
      scores: {label: 'Scores', scope: 'objects', component: PlotScores, buttons: buttons1},
      distance: {label: 'Distance', scope: 'objects', component: PlotDistance, buttons: buttons1},

   })

   // JSON with plots controls (as Widget properties)
   const controls = $state({
      pred: getPlotDiscriminationControls(['new'], model.da.classNames),
      scores: getPlotScoresControls(plsres),
      distance: getPlotDistanceControls(plsres),
   })

   // JSON with currently selected plots parameters
   let params = $state({
      pred: getDefaults(controls.pred),
      scores: getDefaults(controls.scores),
      distance: getDefaults(controls.distance),
   });

   $effect(() => {
      if (model.da.rule === 'hard') {
         controls.pred.showOutliers.hidden = true;
      } else {
         controls.pred.showOutliers.hidden = false;
      }
   });


   // combine main elements to be shared with all plots together
   const zscores = Vector.seq(-10.0, 10, 0.1);
   const densnorm = dnorm(zscores);
   const props = $derived({res:plsres, classres:dares, model:model.pls, classmodel: model.da, data, ncomp, scalars, zscores, densnorm});
</script>

{#snippet buttons1(sel)}
   <Container label="Selected">
      <Button onclick={() => showContributions(sel)} text="plot" />
      {#if (sel >= 0)}
      <Button onclick={() => markOutlier(sel)} text="remove" />
      {/if}
   </Container>
{/snippet}

{#snippet buttons2(sel)}
   <Container label="Selected">
      {#if (res.cal.npred - sel.length >= 2)}
      <Button onclick={() => excludeVars(sel)} text="exclude {sel.length} variables" />
      {:else}
      <p>can not exclude</p>
      {/if}
   </Container>
{/snippet}

<PlotsRouter
   {plots}
   {props}
   {controls}
   bind:params={params}
/>

