   {#snippet info()}
   <h2>PLS1 Discriminant Analysis (binary version)</h2>
   <p>
      Interactive web-application for creating, testing and applying PLS1-DA models for
      discriminant analysis between two classes (binary version). The app implements both conventional
      method based on threshold (hard) as well as soft method based on distributions (soft),
      described in <a href="https://doi.org/10.1016/j.microc.2025.113329">this paper</a>.
      Like any other mda.tools web-apps it does not send any information to the server,
      all calculations are done locally on your device.
   </p>
   <p>
      The application uses <a href="https://doi.org/10.1016/j.aca.2023.341096">Procrustes cross-validation</a> for optimization and quality check on the training stage.
