<script>
   /****************************************************************/
   /* PlotScores                                                   */
   /* --------------------                                         */
   /* shows plot with PLS-DA scores                                */
   /****************************************************************/

   import { ColormapLegend, Segments } from 'svelte-plots-basic/2d';
   import { Vector } from 'mdatools/arrays';
   import { mrange } from 'mdatools/stat';

   import { AXIS_COLOR, MARKERS } from '../../../shared/plots/Defaults.js';
   import { plsres_get } from '../../../shared/wasm/dist/pls.js';
   import { getColors, CLASS_COLORS } from './Controls.svelte.js';

   import Axes from '../../../shared/plots/Axes.svelte';
   import PlotSeries from '../../../shared/plots/PlotSeries.svelte';

   let {
      params,                        // JSON with main plot parameters
      callbacks,                     // JSON with all callback methods
      props,                         // JSON with main properties (model, results, dataset)
      selected,                      // ID of selected object
   } = $props();


   // get the parameters needed for the plot
   const resobj = $derived(props && params && props.res && params.resID ? props.res[params.resID] : null);
   const classresobj = $derived(props && params && props.classres && params.resID ? props.classres[params.resID] : null);


   // get vector with score values for given component
   function getValues(r, a) {
      if (!r || !a || a < 1 || a > r.ncomp) return;
      return new Vector(plsres_get(r, 'xscores', a))
   }

   let xValues = $derived(getValues(resobj, params.comp1));
   let yValues = $derived(getValues(resobj, params.comp2));
   let colors = $derived(getColors(classresobj));
   let pch = $derived(MARKERS[params.resID]);
   let labels = $derived(resobj ? resobj.objlabels : null)

   // reactive expression to generate axis labels
   let xLabel = $derived("LV" + params.comp1);
   let yLabel = $derived("LV" + params.comp2);

   // reactive expression to compute x-axis limits
   let limX = $derived(mrange(xValues));

   // reactive expression to compute x-axis limits
   let ly = $derived(mrange(yValues));
   let dy = $derived(ly ? (ly[1] - ly[0]) * 0.1 : 0)
   let limY = $derived(ly ?[ly[0], ly[1] + dy] : [0, 1]);

   // reactive expression to generate plot title
   let title = 'X-scores';

</script>

{#if xValues && yValues }
<Axes {title} {limX} {limY} {xLabel} {yLabel} fileName='plot-scores' onclick={callbacks.unselectObject}>

   <!-- axis lines (x = 0 and y = 0) -->
   <Segments xStart={[limX[0]]} xEnd={[limX[1]]} yStart={[0]} yEnd={[0]} lineColor={AXIS_COLOR} />
   <Segments xStart={[0]} xEnd={[0]} yStart={[limY[0]]} yEnd={[limY[1]]} lineColor={AXIS_COLOR} />


   <!-- plot series 1 -->
   <PlotSeries
      xValues={xValues}
      yValues={yValues}
      labels={labels}
      marker={pch}
      plotType="p"
      showLabels={params.showLabels}
      colors={colors}
      {selected}
      {callbacks}
   />


   <!-- colormap legend-->
   {#if classresobj && classresobj.ind}
    <ColormapLegend colmap={[CLASS_COLORS[1], CLASS_COLORS[0]]} breaks={[0, 1, 2]} labels={[classresobj.classNames[1], classresobj.classNames[0]]}/>
   {/if}

</Axes>
{/if}

