<script>
   /*************************************************************/
   /* AppPlots component for test app                           */
   /* -----------------------------------                       */
   /* defines plot components, controls and parameters          */
   /*************************************************************/

   import { getDefaults } from 'svelte-controls-basic';
   import { Container, Button } from 'svelte-controls-basic';
   import { c, Vector } from 'mdatools/arrays';
   import { dnorm } from 'mdatools/distributions';

   // global plot router
   import PlotsRouter from '../../../shared/plots/PlotsRouter.svelte';

   // import local plot components
   import PlotFoM from '../plots/PlotFoM.svelte';
   import PlotDiscrimination from '../plots/PlotDiscrimination.svelte';
   import PlotROC from '../plots/PlotROC.svelte';
   import PlotFoMHeatmap from '../plots/PlotFoMHeatmap.svelte';
   import PlotScores from '../plots/PlotScores.svelte';
   import PlotDistance from '../plots/PlotDistance.svelte';

   // import external plot components
   import PlotRegFoM from '../../../shared/plots/PlotFoM.svelte';

   // import local plot controls components
   import { getPlotFoMControls, getPlotDiscriminationControls, getPlotScoresControls, getPlotDistanceControls,
      getPlotROCControls, getPlotFoMHeatmapControls} from '../plots/Controls.svelte.js';

   // import external plot controls components
   import { getPlotFoMControls as getPlotRegFoMControls} from '../../../shared/plots/Controls.svelte.js';



   let {
      res,
      model,
      data,
      scalars,
      markOutlier,
      showContributions,
      setModelParams,
      statMat
   } = $props();


   const plsres = $derived({'test': res.test.pls});
   const dares = $derived({'test': res.test.da});
   const ncomp = $derived(model.da.ncomp);


   // JSON with plot components (scope is needed for providing correct properties)
   const plots = $derived({
      // objects
      pred: {label: 'Discrimination', scope: 'objects', component: PlotDiscrimination, buttons: buttons1},
      fom: {label: 'Figures of merit (plots)', scope: 'stats', component: PlotFoM},
      fomhm: {label: 'Figures of merit (heatmap)', scope: 'options', component: PlotFoMHeatmap, buttons: buttons3},
      roc: {label: 'ROC curve', scope: 'stats', component: PlotROC},

      // statistics
      fom2: {label: 'RMSE', scope: 'stats', component: PlotRegFoM},
      scores: {label: 'Scores', scope: 'objects', component: PlotScores, buttons: buttons1},
      distance: {label: 'Distance', scope: 'objects', component: PlotDistance, buttons: buttons1},
   })

   // JSON with plots controls (as Widget properties)
   const controls = $state({
      pred: getPlotDiscriminationControls(['test'], model.da.classNames, model.da),
      fom: getPlotFoMControls(dares, model.da.classNames),
      fomhm: getPlotFoMHeatmapControls(['test']),
      roc: getPlotROCControls(['test'], model.da.classNames),

      fom2: getPlotRegFoMControls(plsres, 1, ['rmse']),
      scores: getPlotScoresControls(plsres),
      distance: getPlotDistanceControls(plsres),
   });

   // JSON with currently selected plots parameters
   let params = $state({
      pred: getDefaults(controls.pred),
      fom: getDefaults(controls.fom),
      fomhm: getDefaults(controls.fomhm),
      roc: getDefaults(controls.roc),

      fom2: getDefaults(controls.fom2),
      scores: getDefaults(controls.scores),
      distance: getDefaults(controls.distance),
   });


   $effect(() => {
      if (model.da.rule === 'hard') {
         controls.pred.showOutliers.hidden = true;
      } else {
         controls.pred.showOutliers.hidden = false;
      }
   });


   // combine main elements to be shared with all plots together
   const zscores = c(
      Vector.seq(-10.0, -3, 0.5),
      Vector.seq(-3, 3, 0.01),
      Vector.seq(3.0, 10, 0.5),
   );

   const densnorm = dnorm(zscores);
   const props = $derived({res:plsres, classres:dares, model:model.pls, classmodel: model.da, data, ncomp, scalars, zscores, densnorm, statMat});

   // combine all callbacks together
   const name = $derived(model.da.rule === 'hard' ? 'tau' : 'alpha');
</script>

{#snippet buttons1(sel)}
   <Container label="Selected">
      <Button onclick={() => showContributions(sel)} text="plot" />
      {#if (sel >= 0)}
      <Button onclick={() => markOutlier(sel)} text="remove" />
      {/if}
   </Container>
{/snippet}

{#snippet buttons2(sel)}
   <Container label="Selected">
      {#if (res.cal.npred - sel.length >= 2)}
      <Button onclick={() => excludeVars(sel)} text="exclude {sel.length} variables" />
      {:else}
      <p>can not exclude</p>
      {/if}
   </Container>
{/snippet}

{#snippet buttons3(opt)}
   <Container label="Set">
      <Button onclick={() => setModelParams(opt)} text="nLV = {opt[0]}, {name} = {opt[1].toFixed(2)}" />
   </Container>
{/snippet}


<PlotsRouter
   {plots}
   {props}
   {controls}
   bind:params={params}
/>

<style>
   :global(.tab-content-test .plot-controls-area > .plot-selector-fom2) {
      margin-top: 1em;
   }
</style>