<script>
   /*************************************************************/
   /* AppContainer for test set                                 */
   /* -------------------------                                 */
   /* app container for testing a PLS1-DA model                 */
   /*************************************************************/

   import { untrack } from 'svelte';
   import { Button, Container } from 'svelte-controls-basic';
   import { plsmodel_get, plsres_get } from '../../../shared/wasm/dist/pls';
   import { replacer } from '../../../shared/utils.js';
   import { downloadResults, downloadModel, predict, getIndices, getStatMatrices } from '../utils';

   import Outliers from '../../../shared/widgets/Outliers.svelte';
   import Contributions from '../../../shared/components/Contributions.svelte';

   import DataLoader from '../shared/DataLoader.svelte';
   import ModelLoader from '../shared/ModelLoader.svelte';
   import ResultTable from '../shared/ResultTable.svelte';
   import PLSDASettings from '../shared/PLSDASettings.svelte';

   import AppPlots from './AppPlots.svelte';

   // WASM promise and session object from parent app
   let {WASM, backupSession, session = $bindable()} = $props();

   // get part of the session for this tab
   const s = $derived(session['test']);

   // properties for showing contributions
   let selected = $state(-1);
   let contributionIsShown = $state(false);


   // callback function to set object with a give position in plot series
   // as outlier
   function markOutlier(pos) {

      // check that position is valid
      if (pos < 0 || pos > s.testres.pls.nrows - 1) return;

      // find sample ID corresponding to the position
      // this is needed if some of the objects already
      // removed from results as outliers
      const id = s.testres.pls.objindices[pos];

      // add the object to outliers array and reset the model
      if (id >= 0 && !s.outliers.includes(id)) {
         s.outliers.push(id);
         s.testres = {pls: null, da: null};
         s.ind = null;
      }
   }

   // callback to reset outliers
   function resetOutliers() {
      s.outliers = [];
      s.testres = {pls: null, da: null};
      s.ind = null;
   }

   // callback to undo outliers
   function undoOutliers() {
      s.outliers.pop();
      s.testres = {pls: null, da: null};
      s.ind = null;
   }

   // show modal dialog with contributions for selected object
   function showContributions(pos) {
      if (pos < 0) return;
      selected = pos;
      contributionIsShown = true;
   }

   // hide modal dialog with contributions
   function hideContributions() {
      contributionIsShown = false;
      selected = -1;
   }

   // callback to set model parameters from heatmap plot
   function setModelParams(p) {
      s.model.da.ncomp = p[0];
      if (s.model.da.rule === 'hard') {
         s.model.da.threshold = p[1];
      } else {
         s.model.da.alpha = p[1];
      }
   }


   // effect to track changes in dataset and models
   $effect(() => {

      // if training set is set null - reset model and all related parameters
      if (s.testset === null) {
         s.outliers = [];
         s.testres = {pls: null, da: null};
         s.ind = null;
      }

      // if model object is set to null - reset dependent objects
      if (s.model === null) {
         s.testres = {pls: null, da: null};
         s.ind = null;
         s.testset = null;
      }
   });

   $effect(() => {
      if (s.testres && s.testres.pls) {
         s.ind = getIndices(s.testres.pls.Yref.getcolref(1), s.testset.classNames);
      }
   });


   $effect(() => {
      if (s.testres.pls && s.model.da) {
         s.statmat.test = getStatMatrices(s.testres.pls, s.model.da.params, s.ind);
      }
   });


   // recalculate DA results for calibration set when DA parameters have changed
   $effect(() => {
      if (s.testres.pls && s.model.da) {
         s.testres.da = predict(s.testres.pls, s.model.da, s.ind)
      }
   });


   // trigger session backup
   $effect(() => {
      const sessionStr = untrack(() => JSON.stringify(session, replacer));
      backupSession(sessionStr, s.testset, s.model, s.testres.da);
   });
</script>

<div class="app-container">
   {#if contributionIsShown}
   <Contributions
      model_getter={plsmodel_get}
      res_getter={plsres_get}
      model={s.model.pls}
      res={{test: s.testres.pls}}
      ncomp={s.model.da.ncomp}
      {selected}
      closeModal={hideContributions}
   />
   {/if}

   <!-- left column with main app controls -->
   <div class="app-controls">
      <div class="model-loader">
         <ModelLoader bind:model={s.model} />
      </div>

      {#if s.model && s.model.pls}
      <div class="data-loader">
         <DataLoader
            {WASM}
            bind:dataset={s.testset}
            bind:resobj={s.testres.pls}
            outliers={s.outliers}
            model={s.model.pls}
            classMandatory={true}
         />
      </div>

      {#if s.outliers && s.outliers.length > 0 }
      <div class="outliers-pane">
         <Outliers outliers={s.outliers} dataset={s.testset} {resetOutliers} {undoOutliers} />
      </div>
      {/if}


      {#if s.model.pls && s.testres.pls}
      <div class="ncomp-selector">
         <PLSDASettings
            plsmodel={s.model.pls}
            plsres={s.calres.pls}
            ind={s.ind}
            ncomp={s.ncomp_selected}
            bind:damodel={s.model.da}
         />
         <Container label='Download'>
            <div class="buttons">
            <Button onclick={() => downloadResults(s.testres, s.testset, "test", s.model.da.ncomp)} text="Res (test)"/>
            <Button onclick={() => downloadModel(s.model, s.model.da.ncomp)} text="Model" />
            </div>
         </Container>
      </div>
      {#if s.testres.da}
      <div class="result-stats">
         <ResultTable res={{test: s.testres.da}} model={s.model.da} />
      </div>
      {/if}
      {/if}
   {/if}

   </div>

   <!-- right column with help text or with plots and plot controls -->
   <div class="app-content">
      {#if !s.testres.da}
         <div class="help-text">
         {#if !s.model }
            <p>
               In this tab you can test a PLS1-DA classification model developed in <strong>Train</strong> tab by
               applying it to a test set, which contains both predictors and responses.
            </p>
            <p><strong>Step 1.</strong></p>
            <p>Load the previously developed PLS1-DA model from a JSON file.</p>
         {:else if !s.testset}
            <p><strong>Step 2.</strong></p>
            <p>Load your test set from a CSV file. The file structure should be the same as the training/calibration set.
               The column with class labels is mandatory and it should contain the same classes your model was trained for.
               Here is an example how data structure may look like (note, that row labels
               and header are optional):
            </p>
            <div>
               <table>
                  <thead>
                  <tr>
                     <td>Name</td>
                     <td>Class</td>
                     <td>x1</td>
                     <td>x2</td>
                     <td>x3</td>
                  </tr>
                  </thead>
                  <tbody>
                  <tr>
                     <td>O1</td>
                     <td>Red</td>
                     <td>3.8</td>
                     <td>1.3</td>
                     <td>13.6</td>
                  </tr>
                  <tr>
                     <td>O2</td>
                     <td>Red</td>
                     <td>11.1</td>
                     <td>12.3</td>
                     <td>3.3</td>
                  </tr>
                  <tr>
                     <td>O3</td>
                     <td>Blue</td>
                     <td>1.2</td>
                     <td>2.3</td>
                     <td>4.2</td>
                  </tr>
                  </tbody>
               </table>
            </div>
            <p></p>

         {/if}
         </div>
      {:else}

         {#key s.testres.pls && s.model }
         {#if s.testres && s.testres.pls && s.testres.da && s.model && s.model.pls && s.model.da}
         <AppPlots
            {markOutlier}  {showContributions} {setModelParams}
            data={{'test': s.testset}}
            res={{'test': s.testres}}
            model={s.model}
            ncomp={s.ncomp_selected}
            statMat={s.statmat}
         />
         {/if}
         {/key}
      {/if}
   </div>
</div>

<style>

   .app-container {
      font-family: Helvetica, Arial, sans-serif;
      font-size: 1rem;
      color: #404040;
      box-sizing: border-box;
      margin: 0 auto;
      padding: 0.35em 0.25em;
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-areas: "controls content";
      grid-template-rows: 100%;
      grid-template-columns: min(310px, 30%) 1fr;
   }

   .app-controls {
      box-sizing: border-box;
      padding: 0.25em 0.5em;
   }

   .app-content {
      box-sizing: border-box;
      padding: 0 1em 0 0.5em;
      padding: 0.25em 0.25em;
      height: 100%;
   }

   .app-controls > div {
      border-radius: 0.25rem;
   }

   .app-controls > .result-stats {
      background: #e8e8e8;
   }

   .help-text {
      margin-top: 0.25rem;
   }

   .help-text > p, .help-text > div {
      background: #fafafa;
      padding: 0em 1.5em 0.5em 1.5em;
      color: #606060;
      line-height: 1.5em;
      margin: 0 1em 0 0;
   }

   .help-text table {
      border-collapse: collapse;
   }

   .help-text table td{
      padding: 0.25em 0.5em;
      background: #e0f0fa;
      text-align: right;
      border: 1px solid white;
   }

   .help-text table thead tr td{
      font-weight: bold;
      background: #f8f4e0;
   }

   .help-text table td:first-child {
      font-weight: bold;
      background: #f8f4e0;
      padding-right: 1em;
   }

   .help-text table tbody td:nth-child(2) {
      background: #e0e8f0;
      padding-right: 1em;
   }

   .help-text > p:first-child {
      border-top-left-radius: 0.25rem;
      border-top-right-radius: 0.25rem;
      padding-top: 1.5em;
   }

   .help-text > p:last-child {
      border-bottom-left-radius: 0.25rem;
      border-bottom-right-radius: 0.25rem;
      padding-bottom: 1.5em;
   }

   .result-stats {
      padding: 0.5rem 1rem;
      margin-top: 0.25rem;
      font-size: 0.85em;
   }


   @media(min-width:1300px) {
      .app-container {
         grid-template-columns: min(340px, 30%) 1fr;
      }
   }

   @media(min-width:1100px) {
      .app-container {
         grid-template-columns: min(330px, 30%) 1fr;
      }
   }

</style>