<script>
   /*************************************************************/
   /* ResultsTable                                              */
   /* -------------------------                                 */
   /* shows table with PLS1-DA result objects.                  */
   /*************************************************************/

   let { res, model } = $props();

   const statsm = ['TN', 'FP', 'TP', 'FN'];
   const statsv = ['Sensitivity', 'Specificity', 'Efficiency', 'Accuracy'];
   const resIDs = $derived(Object.keys(res));

   let resID = $state(0);

   let out = $derived.by(() => {

      if (!res) return null;
      const r = res[resIDs[resID]];
      const header = model.rule === 'soft' ? ['', ...model.classNames, 'total'] : ['', ...model.classNames];
      let   rows = [];

      // TN, FP, TP, FN
      for (let stat of statsm) {
         let row = [stat];
         let acc = 0;
         for (let c of model.classNames) {
            const v = r.v[c].res[model.ncomp - 1][stat];
            acc += v;
            row.push(v);
         }
         if (model.rule === 'soft') {
            row.push(acc);
         }
         rows.push(row);

      }

      // FoMs
      for (let stat of statsv) {
         let row = [stat];
         const statl = stat.toLowerCase()
         for (let c of r.classNames) {
            const v = r.v[c].stat[statl][model.ncomp - 1];
            row.push(v.toFixed(3));
         }
         if (model.rule === 'soft') {
            const v = r.v['total'].stat[statl][model.ncomp - 1];
            row.push(v.toFixed(3));
         }
         rows.push(row);
      }


      return {header, rows};
   });
</script>


{#if out}
<table>
   <thead>
   <tr>
      {#each out.header as v}
      <th>{v}</th>
      {/each}
   </tr>
   </thead>
   <tbody>
   {#each out.rows as row}
   <tr>
      {#each row as v}
      <td>{v}</td>
      {/each}
   </tr>
   {/each}
   </tbody>
   </table>

   {#if resIDs.length > 1}
   <div class="switcher">
      {#each resIDs as rid, i}
      <input id={rid} type="radio" name="resID" bind:group={resID} value={i}>
      <label for={rid}>{rid}</label>
      {/each}
   </div>
   {/if}
{/if}

